* {
    padding: 0;
    margin: 0;
    border: 0;
 }
 
 *,
 *:before,
 *:after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
 }
 
 :focus,
 :active {
    outline: none;
 }
 
 a:focus,
 a:active {
    outline: none;
 }
 
 nav,
 footer,
 header,
 aside {
    display: block;
 }
 
 html,
 body {
    height: 100%;
    width: 100%;
    font-size: 100%;
    font-family: 'Montserrat', sans-serif;
    color: $black;
    line-height: 1;
    font-size: 14px;
    min-width: 320px;
    background-color: white;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
 }
 
 input,
 button,
 textarea {
    font-family: inherit;
 }
 
 input::-ms-clear {
    display: none;
 }
 
 button {
    font-size: inherit;
    color: inherit;
    cursor: pointer;
    background: transparent;
 }
 
 button::-moz-focus-inner {
    padding: 0;
    border: 0;
 }
 
 a,
 a:visited {
    color: inherit;
    text-decoration: none;
 }
 
 a:hover {
    text-decoration: none;
 }
 
 ul li {
    list-style: none;
 }
 
 img {
    vertical-align: top;
 }
 
 h1,
 h2,
 h3,
 h4,
 h5,
 h6 {
    font-size: inherit;
    font-weight: inherit;
    line-height: 1.2;
 }

 p{
   line-height: 1.4;
 }