.header {
   width: 100%;
   height: 70px;
   background-color: #039be5;
   color: white;
}

.header__inner {
   display: flex;
   height: 100%;
   justify-content: space-between;
}

.header__menu {
   display: flex;
   font-size: 16px;
}

.header-logo {
   padding-right: 20px;
   display: flex;
   align-items: center;
   justify-content: center;

   img{
      width: 65px;
      height: 65px;
   }
}

.header__nav-list {
   display: flex;
   height: 100%;

   li {
      height: 100%;

      a {
         color: inherit;
         height: 100%;
         display: flex;
         align-items: center;
         padding: 0 15px;
         opacity: 0.8;
      }

      a.nav-link--active {
         background-color: rgba(255, 255, 255, 0.144);
         opacity: 1;
      }

      a:hover{
        opacity: 1;
      }
   }
}

.header__user {
   display: flex;
   align-items: center;
   justify-content: center;
   margin-left: 10px;
}

.header__user-avatar {
   width: 45px;
   height: 45px;
   background-color: #fff;
   border-radius: 50%;
   display: flex;
   justify-content: center;
   align-items: center;
   margin-right: 8px;

   img {
      display: block;
      object-fit: cover;
      width: 85%;
   }
}

.header__user-logout {
   display: flex;
   align-items: center;
   margin-top: 2px;

   span {
      margin-left: 4px;
      transition: all 0.2s ease 0s;
   }

   svg {
      g {
         transition: all 0.2s ease 0s;
         fill: rgb(255, 255, 255);
      }
   }

   &:hover {
      span {
         color: rgb(255, 112, 112);
      }

      svg {
         g {
            fill: rgb(255, 112, 112);
         }
      }
   }
}


.header__user-signin{
   display: flex;
   align-items: center;
   margin-top: 2px;

   span {
      margin-left: 4px;
      transition: all 0.2s ease 0s;
   }

   svg {

      transform: scale(-1);
      g {
         transition: all 0.2s ease 0s;
         fill: rgb(255, 255, 255);
      }
   }

   &:hover {
      span {
         color: rgb(59, 59, 59);
      }

      svg {
         g {
            fill: rgb(59, 59, 59);
         }
      }
   }
}
